.threed {
    margin-top: 4em;
    background-color: var(--color2);
    min-height: calc(100vh - 4em);
}

.threednadpisy {
    padding: 3em 2em;
}

.threedprojekt {
    padding: 4em;
    display: flex;
    flex-direction: column;
    gap: 0.5em;
    background: linear-gradient(var(--color1), var(--color2));
    filter: brightness(90%);
}

.threedprojekt:hover {
    filter: brightness(125%);
    transform: scale(101%);
}

.threedprojekt:hover h3 {
    color: var(--color7);
}

.threedprojekt:nth-child(odd) {
    background-image: linear-gradient(to right, var(--color1), var(--color2));
}

.threedprojekt:nth-child(even) {
    background-image: linear-gradient(to left, var(--color1), var(--color2));
    text-align: right;
}

.threedhistoria {
    background-color: var(--color2);
    margin-top: 3em;
}


.threedkalk {
    background-color: var(--color2);
    min-height: 100vh;
    margin-top: 4em;
    padding: 2em 2em 0 2em;
    display: flex;
    flex-direction: column;
    gap: 1.5em;
}
.threedkalkobsah {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 4em;
}

.threedkalk select {
    margin: 0;
    padding: 1em;
}

.threedkalk .smallbutton {
    padding: 0.5em;
    width: 2.5em;
    height: 2.5em;
}

.threedkalknadpisy p span {
    color: white;
    cursor: pointer;
}
.threedkalknadpisy p span:hover {
    color: var(--color7);
}
.threedkalkinputy {
    display: flex;
    flex-direction: column;
    gap: 1.5em;
}

.threedkalkinputy > div {
    padding: 2em;
    border: 2px solid white;
    border-radius: 1em;
    width: max-content;
    display: flex;
    flex-direction: column;
    gap: 1em;
}

.threedkalkinput {
    display: flex;
    flex-direction: column;
    gap: 0.5em;
    padding-left: 0.5em;
}

.threedkalkinput > div {
    display: flex;
    align-items: center;
    gap: 0.5em;
}
.threedkalkmodel {
    width: 100%;
    margin-top: -4em;
    display: flex;
    flex-direction: column;
    gap: 0.5em;
    padding: 1em;
}
.threedkalkmodel img {
    width: 100%;
}


.threedkalkmodel > div:last-child {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 1em;
    padding: 0.75em;
}
.threedkalkmodel > div:last-child img {
    height: 2em;
    width: 2em;
}
.threedkalkmodel > div:last-child img:hover {
    transform: scale(110%);
}


.threedmodely {
    background-color: var(--color2);
    min-height: 100vh;
    margin-top: 4em;
    padding: 2em 2em 0 2em;
}

.threedmodelymodely {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2em;
    padding-top: 2em;
}

.threedmodel {
    border: 1px solid white;
    border-radius: 16px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.threedmodelpismenka {
    padding: 1em;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.threedmodelpismenka > div {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 1em;
}

.threedmodelpismenka img {
    height: 1.5em;
}

.threedmodelpismenka img:hover {
    transform: scale(110%);
}

.threedmodelyafterbutton {
    margin-top: 2em;
    width: max-content;
}

.threedmodelyafterbutton button {
    padding: 1em 1.5em;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1em;
}

.threedmodelyafterbutton img {
    height: 2em;
}


.threedfilamenty {
    background-color: var(--color2);
    min-height: 100vh;
    margin-top: 4em;
    padding: 2em 2em 0 2em;
}

.threedfilamentynadpisy {
    padding-bottom: 1em;
}

.threedfilamentyfilamenty {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 2em;
}

.threedfilament {
    display: flex;
    flex-direction: column;
    border: 1px solid white;
    border-radius: 12px;
    height: max-content;
}

.threedfilament > div {
    padding: 1em;
    display: flex;
    flex-direction: column;
    gap: 0.5em;
}

.threedfilament > div > div:first-child {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.threedfilament > div > div:first-child > div:last-child {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.threedfilament > div > div:first-child > div:last-child p:first-child {
    font-size: 1.125em;
}


.threedfilament img {
    width: 100%;
    border-radius: 12px 12px 0 0;
}

.threedfilamentyinput {
    display: flex;
    gap: 1em;
    justify-content: start;
    align-items: center;
    margin-bottom: 1em;
    margin-left: 0.5em;
}

.threedfilamentyinput select {
    margin: 0;
    padding: 1em;
}
.threedfilamentyinput button {
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}
.threedfilamentyinput button svg {
    width: 2em;
    height: 2em;
    margin: 0.5em;
    fill: white;
}


.disabledfilament {
    filter: contrast(0.5);
}
.disabledfilament::after {
    content: "Tohto filamentu nie je dostatok pre vytlaÄenie tvojho modelu";
    padding: 0 1em 1em 1em;

}


.threedgaleria {
    background-color: var(--color2);
    min-height: 100vh;
    margin-top: 4em;
    padding: 2em 2em 0 2em;
}
